@media screen and (max-width: 1166px) {
    body {
        min-width: 320px
    }

    #header {
        margin: 0 -10px;
        padding: 17px 10px 15px;
    }
}

@media screen and (max-width: 1050px) {
    .main-section .head h1 {
        width: 20%;
    }

    .main-section .head .holder {
        width: 78%;
    }

}

@media screen and (max-width: 960px) {
    .main-section .head h1 {
        float: none;
        width: 50%;
        margin: 0 0 20px;
    }

    .main-section .head .holder {
        width: 100%;
    }

    .result-paging,
    .create-project {
        width: 45%;
    }

    .nav-controller a i {
        display: block;
        margin: 3px auto;
    }

    .table-dash-board .col4 {
        width: 18%;
    }
}

@media screen and (max-width: 820px) {
    #sidebar {
        width: 250px;
    }

    .accordion .slide-holder {
        padding: 10px;
    }

    .nav-controller ul {
        display: inherit;
    }

    .nav-controller ul li {
        width: 50%;
        height: 50px;
        float: left;
        display: inherit;
        border: #ccc solid;
        box-sizing: border-box;
        border-width: 0 1px 1px 0;
        -moz-box-sizing: border-box;
        -webkit-box-sizing: border-box;
    }

    .form-account-setting .field-area,
    .form-account-setting .row {
        max-width: none;
    }

    .form-account-setting button[type="submit"] {
        width: 100%;
        left: auto;
        display: block;
    }

    .results-list li .number {
        font-size: 30px;
        line-height: 36px;
    }
}

@media screen and (max-width: 767px) {
    #sidebar {
        /*float: none;*/
        width: inherit;
        margin: 0 0 10px;
    }

    .breadcrumb,
    .btns-area {
        float: none;
        margin: 0 0 10px;
    }

    .btns-area {
        margin: 0;
    }

    .panel > time {
        text-align: center;
    }

    .table-dash-board tr,
    .table-dash-board tbody,
    .table-dash-board td {
        clear: both;
        width: 100%;
        float: left;
    }

    .table-dash-board tbody {
        box-sizing: border-box;
        -moz-box-sizing: border-box;
        -webkit-box-sizing: border-box;
        border-left: #f5f5f5 solid 130px;
    }

    .table-dash-board tr:nth-child(even) {
        background: #fafafa;
    }

    .table-dash-board td {
        width: 100% !important; /* 116.4% */
        border-width: 0 0 1px;
        padding: 0 0 0 130px;
        margin: 0 0 0 -130px;
    }

    .table-dash-board td:before {
        width: 114px;
        content: attr(title);
        display: inline-block;
        vertical-align: middle;
        margin: 0 0 0 -120px;
    }

    .table-dash-board td > div {
        overflow: visible;
        max-width: 500px;
        display: inline-block;
        vertical-align: middle;
    }

    .section-footer {
        text-align: center;
    }

    .result-counter {
        float: none;
        display: block;
        margin: 0 0 10px;
    }

    .section-footer .paging {
        float: none;
        vertical-align: top;
        display: inline-block;
    }

    .main-section.white {
        padding: 10px;
    }

    .drag-file .note {
        font-size: 18px;
        line-height: 22px;
    }

    .drag-file .note span {
        display: inline;
    }

    .form-new-position .field-list label {
        float: none;
        display: block;
        padding: 0 0 10px;
    }

    .date-select:first-child,
    .date-select {
        float: none;
        display: block;
        width: inherit;
        margin: 10px 0 0;
    }

    .date-select:first-child {
        margin: 0;
    }

    .table-dash-board thead {
        display: none;
    }

    .form-new-position button[type="submit"],
    .form-new-position button[type="reset"] {
        width: 49%;
        margin: 0;
    }

    .form-new-position button[type="reset"] {
        float: left;
    }
}

@media screen and (max-width: 580px) {
    .logo {
        float: none;
        margin: 0 0 10px;
    }

    .nav-settings {
        float: none;
        padding: 10px 0 0;
        text-align: center;
        border-top: 1px #777 solid;
    }

    .nav-settings ul li {
        float: none;
        margin: 0 5px 10px;
        vertical-align: top;
        display: inline-block;
    }

    .nav-settings ul li.author {
        top: 10px;
        right: 10px;
        position: absolute;
    }

    .main-section {
        overflow: visible;
    }

    .panel-holder {
        padding: 10px;
    }

    .main-section .head {
        padding: 9px 13px;
    }

    .main-section .head h1 {
        width: inherit;
        position: relative;
        text-align: center;
        background: #0077b9;
        margin: -22px 20px 20px;
        padding: 15px 5px 10px;
        border-radius: 0 0 5px 5px;
        -webkit-border-radius: 0 0 5px 5px;
    }

    .main-section .head h1:after,
    .main-section .head h1:before {
        top: 0;
        z-index: -1;
        right: 100%;
        width: 30px;
        content: '';
        height: 30px;
        position: absolute;
        background: #484a4c;
        margin: 7px -15px 0 0;
        transform: rotate(45deg);
        -moz-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        -o-transform: rotate(45deg);
        -webkit-transform: rotate(45deg);
    }

    .main-section .head h1:after {
        right: auto;
        left: 100%;
        margin: 7px 0 0 -15px;
    }

    .result-paging,
    .create-project {
        float: none;
        clear: both;
        width: inherit;
        margin: 0 0 10px;
    }

    .result-paging {
        overflow: hidden;
    }

    .section-footer .paging {
        display: inherit;
        padding: 0 0 49px;
        position: relative;
    }

    .paging ul {
        left: 0;
        right: 0;
        bottom: 0;
        position: absolute;
    }

    .paging ul li {
        float: none;
        margin: 0 -2px;
        vertical-align: top;
        display: inline-block;
    }

    .paging .btn.btn-next,
    .paging .btn-last.btn {
        float: right;
    }

    .form-account-setting label {
        float: none;
        width: inherit;
        display: block;
        text-align: left;
        padding: 0 3px 5px;
    }

    .results-list li {
        padding: 0 0 10px;
        width: 33.33333333333333%;
    }

    .tabset {
        letter-spacing: normal;
    }

    .tabset li a {
        padding: 10px 9px;
    }
}

.hidden {
  display: none;
}